import React, { Component } from 'react'
import { TextInputField, Position, Tooltip, InfoSignIcon } from 'evergreen-ui'

class FormText extends Component {
  constructor(props)
  {
    super(props)
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(event)
  {
    this.props.field.onChange(this.props.field.key, event.target.value)
  }
  render() {
    return (
      <div className="form-block__field">
        {(this.props.field.label?(
          <label className="form-block__field-label">
            <div className="form-block__field-label-wrap">
              {this.props.field.label}
              {this.props.field.description?(
                <Tooltip content={this.props.field.description} appearance='card' position={Position.RIGHT}>
                  <InfoSignIcon />
                </Tooltip>
              ):null}
            </div>
          </label>
        ):null)}
        {(this.props.field.description?(
          <small className="form-block__field-describe">{this.props.field.description}</small>
        ):null)}
        <div className="form-block__field-wrap">
          <TextInputField
            label={""}
            disabled={this.props.field.isDisabled === true}
            // isInvalid={true}
            // validationMessage="This field is required"
            onChange={this.handleChange}
            value={this.props.field.value}
            placeholder={this.props.field?.placeholder}
          />
        </div>
      </div>
    )
  }
}

export default FormText;
